    url('fonts/icomoon.ttf?5euwgi') format('truetype'),
    url('fonts/icomoon.woff?5euwgi') format('woff'),
    url('fonts/icomoon.svg?5euwgi#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
